import React from 'react'
import { graphql, Link } from 'gatsby'

export default ({ data }) => (
  <div className="container mt-4">
    <h1>Pages</h1>
    <ul>
      {data.allSitePage.edges
        .filter(
          ({ node }) => node.path !== '/dev-404-page/' && node.path !== '/'
        )
        .map(({ node }) => (
          <li>
            <Link to={node.path}>{node.path}</Link>
          </li>
        ))}
    </ul>
  </div>
)

export const query = graphql`
  query {
    allSitePage {
      edges {
        node {
          path
        }
      }
    }
  }
`
